import { useState } from "react";
import Cookies from "js-cookie";
import axios from "axios";
import HeaderAdmin from "./HeaderAdmin";
import SidebarAdmin from "./SidebarAdmin";

function CreateCourse() {
  var user = Cookies.get("user");
  if (user !== undefined) {
    user = JSON.parse(user);
    var auth = user.permission;
    console.log(user.access_token);
  }
  if (auth !== "ADMIN" && auth !== "TEACHER") {
    window.location.href = "/login";
  }
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [views, setViews] = useState("");
  const [status, setStatus] = useState(1);
  const [thumbnail, setThumbnail] = useState(null);
  // const [videoDemoUrl, setVideoDemoUrl] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const handleCreateCourse = async (e) => {
    e.preventDefault();
    console.log(`Bearer ${user.access_token}`);
    try {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("description", description);
      formData.append("price", price);
      formData.append("views", views);
      formData.append("status", status);
      formData.append("thumbnail", thumbnail);
      // formData.append("video_demo_url", videoDemoUrl);

      const response = await axios.post(
        "http://api.course-selling.id.vn/api/course/create",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data", //upload file
            Authorization: `Bearer ${user.access_token}`,
          },
        }
      );

      if (response.data) {
        alert("To kha hc thnh cng!");
        window.location.href = "/admin/create-course";
      }
    } catch (error) {
      // console.error("Fail to create course: ", error);
      setErrorMessage("C xy ra li khi to kha hc ny.");
    }
  };
  return (
    <div className="Admin">
      <div className="container-fluid">
        <div className=" HeaderAdmin SidebarAdmin">
          <div className="row vh-100">
            <div className="col-lg-3 p-0">
              <SidebarAdmin />
            </div>

            <div className="col-lg-9">
              <HeaderAdmin />
              <div className="custom-border-top">
                <form
                  className="custom-form m-auto"
                  onSubmit={handleCreateCourse}
                >
                  <div className="custom-div-1">To kha hc</div>
                  <div className="mb-3">
                    <label className="form-label">Tn kha hc: </label>
                    <input
                      className="form-control"
                      placeholder="Tn kha hc"
                      type="text"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label">M t: </label>
                    <textarea
                      className="form-control"
                      placeholder="M t ni dung kha hc"
                      value={description}
                      onChange={(e) => setDescription(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label">Gi: (VND)</label>
                    <input
                      min={0}
                      className="form-control"
                      placeholder="Gi kha hc"
                      type="number"
                      value={price}
                      onChange={(e) => setPrice(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label">Lt xem: </label>
                    <input
                      min={0}
                      className="form-control"
                      placeholder="S lt xem"
                      type="number"
                      value={views}
                      onChange={(e) => setViews(e.target.value)}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label className="form-label">nh minh ha: </label>
                    <input
                      className="form-control"
                      type="file"
                      onChange={(e) => setThumbnail(e.target.files[0])}
                      required
                    />
                  </div>
                  {/* <div className="mb-3">
                <label className="form-label">
                  Video gii thiu kha hc:{" "}
                </label>
                <input
                  className="form-control"
                  type="file"
                  onChange={(e) => setVideoDemoUrl(e.target.files[0])}
                />
              </div> */}
                  <div className="mb-3">
                    <div className="form-label"> Trng thi: </div>
                    <div className="mb-1">
                      <input
                        type="radio"
                        value="1"
                        checked={status === 1}
                        onChange={() => setStatus(1)}
                      />
                      <span className="mx-2">Hin th kha hc</span>
                    </div>
                    <div className="mb-1">
                      <input
                        type="radio"
                        value="0"
                        checked={status === 0}
                        onChange={() => setStatus(0)}
                      />
                      <span className="mx-2">n kha hc</span>
                    </div>
                  </div>
                  <button className="btn btn-primary" type="submit">
                    Lu kha hc
                  </button>
                  {errorMessage && (
                    <p style={{ color: "red" }}>{errorMessage}</p>
                  )}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateCourse;
